@import url(https://fonts.googleapis.com/earlyaccess/notosanskr.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* font-start */
@font-face { 
  font-family: 'BMJUA'; 
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_one@1.0/BMJUA.woff') format('woff'); 
  font-weight: normal; 
  font-style: normal; 
}
@font-face {
  font-family: 'BBTreeTL';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_nine_@1.1/BBTreeTL.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face { 
  font-family: 'GmarketSansBold'; 
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansBold.woff') format('woff'); 
  font-weight: normal; 
  font-style: normal; 
}
@font-face { 
  font-family: 'GmarketSansMedium'; 
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff'); 
  font-weight: normal; font-style: normal; 
}
@font-face { 
  font-family: 'GmarketSansLight'; 
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff') format('woff'); 
  font-weight: normal; font-style: normal; 
}
@font-face { 
  font-family: 'TmonMonsori'; 
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/TmonMonsori.woff') format('woff'); 
  font-weight:normal; 
  font-style: normal; 
}
/* font-end */

.App {
  padding: 50px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  font-size: 14px;
  font-family: "Noto Sans KR", sans-serif;
}

.App--loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 30px;
  font-family: "GmarketSansBold";
}



/* reset */
p {
    margin: 0;
}
/* reset-end */
.movie {
    width: 550px;
    height: 290px;
    margin: 20px 10px;
    padding: 20px;
    position: relative;
    background-color: rgba(0,0,0,0.1);
    border-radius: 10px;
    display: flex;
    flex-shrink: 0;
}

.movie_poster_box {
    width: 35%;
    margin-right: 5%;
    position: relative;
}

.movie_detail_box {
    width: 60%;
}

.movie_poster_box .movie_poster {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: all 0.5s ease;
}

.movie_poster_box .movie_poster:hover {
    top: -50px;
}

.movie_detail_box .movie_title {
    font-size: 25px;
    font-weight: 700;
}

.movie_genres_box {
    margin: 5px 0 10px 0;
    display: flex;
    flex-wrap: wrap;
}

.movie_genres_box .movie_genres {
    margin: 5px 5px 0 0;
    background-color: rgb(142, 204, 255);
    padding: 2px 5px;
    border-radius: 5px;
    font-family: 'BMJUA'; 
}
